<template>
  <base-layout>
    <h2 class="text-center my-30px">
      总收益:
      <strong> {{ formattedTotal }} milk </strong>
      <br/>
    </h2>
    <br/>
    <a-row :gutter="[16, 16]">
      <a-col :md="12" :sm="24">
        <a-alert type="info" message="钱包地址" class="h-284px wallet-card">
          <template #description>
            <template v-if="account">
              <span
                  style="cursor: pointer; margin-right: 5px"
                  id="account"
                  :data-clipboard-text="account"
              >
                {{ account }}
              </span>
              <span v-if="copyStatus" style="color: #19be6b">
                <a-icon type="check-circle"/>
                复制成功
              </span>
              <a-tooltip v-else content="点击复制" placement="right">
                <a-icon
                    id="copy-btn"
                    type="copy"
                    :data-clipboard-text="account"
                    size="20"
                    style="color: #1890ff; cursor: pointer"
                />
              </a-tooltip>
            </template>
            <span v-else>暂无地址信息</span>
            <div style="word-break: break-all">
              <p> 卡片数量 {{ cardsCount }} 张</p>
            </div>
          </template>
        </a-alert>
      </a-col>
      <a-col :md="12" :sm="24">
        <a-alert type="warning" message="使用说明">
          <template #description>
            <div style="word-break: break-all">
              <p>
                1. 本服务每十张卡需要支付 0.001 bnb (0.5u
                左右)，不足十张按十张计算
              </p>
            </div>
            <p>
              2. 选中卡进行领取工资，
              <strong>
                收菜时会先向本服务转服务费，成功之后会批量弹出领取工资交易
              </strong>
            </p>
            <p>
              3. 本工具完全开源，开源地址
              <strong>
                <a href="https://github.com/0x0baby/0x0baby.github.io" target="_blank">https://github.com/0x0baby/0x0baby.github.io </a>
              </strong>
            </p>
          </template>
        </a-alert>
      </a-col>
    </a-row>
    <br/>
    <a-row>
      <a-col>
        <PropsTable
            :data-source="propsDataSource"
            :loading="loading"
            v-model="selectedTokens"
            @fire="getAward"
            @harvestAll="getAllAward"
            @quitAll="quitAll"
            @quitSelect="quitSelect"
        />
      </a-col>
    </a-row>
  </base-layout>
</template>

<script>
import mixins from "@/mixins";
import PropsTable from "@/components/harvest/propsTable.vue";
import ClipboardJs from "clipboard";

export default {
  name: "Harvest",
  mixins: [mixins],
  components: {
    PropsTable,
  },
  data() {
    return {
      rate: 0.0,
      total: 0,
      tokens: [],
      selectedTokens: [],
      loading: true,
      feeRate: 10,
      baseFee: 0.001,
      propsDataSource: [],
      copyStatus: 0,
      goldPrice: 0,
      work: null,
      partTimeWork: null,
      playContract: null,
      feeContract: null,
      cardsCount: 0,
    };
  },
  computed: {
    formattedTotal() {
      const total = +this.total;
      if (total) {
        return total.toFixed(2);
      }
      return total;
    },
    totalUsd() {
      const total = +this.total;
      if (total) {
        return (total * this.goldPrice).toFixed(2);
      }
      return 0;
    },
  },
  async created() {
    await this.initWeb3Account();
    if (!this.account) {
      return;
    }

    this.initContract();
    await this.getGoldInfo();
  },
  mounted() {
    window.getProperty = this.getProperty;
    const accountBtn = new ClipboardJs("#account");
    const copyBtn = new ClipboardJs("#copy-btn");

    const successCb = () => {
      this.copyStatus = true;
      setTimeout(() => {
        this.copyStatus = false;
      }, 3000);
    };

    copyBtn.on("success", successCb);
    accountBtn.on("success", successCb);
  },
  methods: {
    async getGoldInfo() {
      const gold = this.web3.utils.toBN(0);
      const propsDataSource = []
      const tokenIds = await this.getWorkingTokenIds(this.account)
      for (const tokenId of tokenIds) {
        const tokenProperty = await this.getProperty(tokenId)
        let igold = await this.getNftAward(tokenId)
        igold = this.web3.utils.toBN(igold);
        this.tokens.push({
          token_id: tokenId,
          gold: this.web3.utils.fromWei(igold),
        });
        tokenProperty.gold = this.web3.utils.fromWei(igold);
        tokenProperty.work = '奶牛站'
        propsDataSource.push(tokenProperty);
        gold.iadd(igold)
        this.cardsCount += 1;
      }

      const warehouseTokenIds = await this.getWarehouseWorkingTokenIds(this.account)
      for (const tokenId of warehouseTokenIds) {
        const tokenProperty = await this.getProperty(tokenId)
        let igold = await this.getWarehouseNftAward(tokenId)
        igold = this.web3.utils.toBN(igold);
        this.tokens.push({
          token_id: tokenId,
          gold: this.web3.utils.fromWei(igold),
        });
        tokenProperty.gold = this.web3.utils.fromWei(igold);
        tokenProperty.work = '仓库'
        propsDataSource.push(tokenProperty);
        gold.iadd(igold)
        this.cardsCount += 1;
      }

      const energyTokenIds = await this.getEnergyWorkingTokenIds(this.account)
      for (const tokenId of energyTokenIds) {
        const tokenProperty = await this.getProperty(tokenId)
        let igold = await this.getEnergyNftAward(tokenId)
        igold = this.web3.utils.toBN(igold);
        this.tokens.push({
          token_id: tokenId,
          gold: this.web3.utils.fromWei(igold),
        });
        tokenProperty.gold = this.web3.utils.fromWei(igold);
        tokenProperty.work = '能源站'
        propsDataSource.push(tokenProperty);
        gold.iadd(igold)
        this.cardsCount += 1;
      }

      const gymTokenIds = await this.getGymWorkingTokenIds(this.account)
      for (const tokenId of gymTokenIds) {
        const tokenProperty = await this.getProperty(tokenId)
        let igold = await this.getGymNftAward(tokenId)
        igold = this.web3.utils.toBN(igold);
        this.tokens.push({
          token_id: tokenId,
          gold: this.web3.utils.fromWei(igold),
        });
        tokenProperty.gold = this.web3.utils.fromWei(igold);
        tokenProperty.work = '健身房'
        propsDataSource.push(tokenProperty);
        gold.iadd(igold)
        this.cardsCount += 1;
      }

      const darkTokenIds = await this.getDarkWorkingTokenIds(this.account)
      for (const tokenId of darkTokenIds) {
        const tokenProperty = await this.getProperty(tokenId)
        let igold = await this.getDarkNftAward(tokenId)
        igold = this.web3.utils.toBN(igold);
        this.tokens.push({
          token_id: tokenId,
          gold: this.web3.utils.fromWei(igold),
        });
        tokenProperty.gold = this.web3.utils.fromWei(igold);
        tokenProperty.work = '黑暗城堡'
        propsDataSource.push(tokenProperty);
        gold.iadd(igold)
        this.cardsCount += 1;
      }

      const postTokenIds = await this.getPostWorkingTokenIds(this.account)
      for (const tokenId of postTokenIds) {
        const tokenProperty = await this.getProperty(tokenId)
        let igold = await this.getPostNftAward(tokenId)
        igold = this.web3.utils.toBN(igold);
        this.tokens.push({
          token_id: tokenId,
          gold: this.web3.utils.fromWei(igold),
        });
        tokenProperty.gold = this.web3.utils.fromWei(igold);
        tokenProperty.work = '邮局'
        propsDataSource.push(tokenProperty);
        gold.iadd(igold)
        this.cardsCount += 1;
      }

      const airportTokenIds = await this.getAirportWorkingTokenIds(this.account)
      for (const tokenId of airportTokenIds) {
        const tokenProperty = await this.getProperty(tokenId)
        let igold = await this.getAirportNftAward(tokenId)
        igold = this.web3.utils.toBN(igold);
        this.tokens.push({
          token_id: tokenId,
          gold: this.web3.utils.fromWei(igold),
        });
        tokenProperty.gold = this.web3.utils.fromWei(igold);
        tokenProperty.work = '机场'
        propsDataSource.push(tokenProperty);
        gold.iadd(igold)
        this.cardsCount += 1;
      }

      this.propsDataSource = propsDataSource;
      this.loading = false;

      this.total = this.web3.utils.fromWei(gold.toString(), "ether");
    },
    calculateFee(tokens) {
      return `${Math.ceil(tokens.length / this.feeRate) * this.baseFee}`;
    },
    async quitSelect() {
      const selectedTokens = this.selectedTokens;
      console.log('select tokens: ', selectedTokens)
      if (!selectedTokens.length) {
        this.$message.error("至少选择一项后操作");
        return;
      }

      const tokens = {};
      for (const i of selectedTokens) {
        const work = i.work
        if (tokens[work] === undefined) {
          tokens[work] = []
        }
        tokens[work].push(i)
      }
      // 支付费用
      this.payFee(
          selectedTokens, // 收菜
          (error) => {
            if (error != undefined) {
              console.log("error: ", error);
              return;
            }
            if (tokens["奶牛站"] !== undefined) {
              for (const token of tokens["奶牛站"]) {
                this.milkStationContract.methods.withdraw(token.tokenId).send({'from': this.account})
              }
            }
            if (tokens["仓库"] !== undefined) {
              for (const token of tokens["仓库"]) {
                this.warehouseContract.methods.withdraw(token.tokenId).send({'from': this.account})
              }
            }
            if (tokens["能源站"] !== undefined) {
              for (const token of tokens["能源站"]) {
                this.energyStationContract.methods.withdraw(token.tokenId).send({'from': this.account})
              }
            }

            if (tokens["健身房"] !== undefined) {
              for (const token of tokens["健身房"]) {
                this.gymContract.methods.withdraw(token.tokenId).send({'from': this.account})
              }
            }
            if (tokens["黑暗城堡"] !== undefined) {
              for (const token of tokens["黑暗城堡"]) {
                this.darkCastleContract.methods.withdraw(token.tokenId).send({'from': this.account})
              }
            }
            if (tokens["邮局"] !== undefined) {
              for (const token of tokens["邮局"]) {
                this.postOfficeContract.methods.withdraw(token.tokenId).send({'from': this.account})
              }
            }
            if (tokens["机场"] !== undefined) {
              for (const token of tokens["机场"]) {
                this.airportContract.methods.withdraw(token.tokenId).send({'from': this.account})
              }
            }

          }
      ).catch(() => {
        this.$message.error("支付失败");
      });

    },
    async quitAll() {
      // 支付费用
      const selectedTokens = this.selectedTokens;
      const workArray = selectedTokens.map((item) => item.work).filter((v, i, s) => s.indexOf(v) === i)
      this.payFee(
          selectedTokens,
          (error) => {
            if (error != undefined) {
              console.log("error: ", error);
              return;
            }
            if (workArray.includes("奶牛站")) {
              this.milkStationContract.methods.withdrawAll().send({'from': this.account})
            }
            if (workArray.includes("仓库")) {
              this.warehouseContract.methods.withdrawAll().send({'from': this.account})
            }
            if (workArray.includes("能源站")) {
              this.energyStationContract.methods.withdrawAll().send({'from': this.account})
            }
            if (workArray.includes("健身房")) {
              this.gymContract.methods.withdrawAll().send({'from': this.account})
            }
            if (workArray.includes("黑暗城堡")) {
              this.darkCastleContract.methods.withdrawAll().send({'from': this.account})
            }
            if (workArray.includes("邮局")) {
              this.postOfficeContract.methods.withdrawAll().send({'from': this.account})
            }
            if (workArray.includes("机场")) {
              this.airportContract.methods.withdrawAll().send({'from': this.account})
            }
          }
      )
    },
    async getAllAward() {
      // 支付费用
      const selectedTokens = this.selectedTokens;
      const workArray = selectedTokens.map((item) => item.work).filter((v, i, s) => s.indexOf(v) === i)
      console.log("work array", workArray)
      this.payFee(
          selectedTokens, // 收菜
          (error) => {
            if (error != undefined) {
              console.log("error: ", error);
              return;
            }
            if (workArray.includes("奶牛站")) {
              this.harvestAll()
            }
            if (workArray.includes("仓库")) {
              this.warehouseContract.methods.harvestAll().send({'from': this.account})
            }
            if (workArray.includes("能源站")) {
              this.energyStationContract.methods.harvestAll().send({'from': this.account})
            }
            if (workArray.includes("健身房")) {
              this.gymContract.methods.harvestAll().send({'from': this.account})
            }
            if (workArray.includes("黑暗城堡")) {
              this.darkCastleContract.methods.harvestAll().send({'from': this.account})
            }
            if (workArray.includes("邮局")) {
              this.postOfficeContract.methods.harvestAll().send({'from': this.account})
            }
            if (workArray.includes("机场")) {
              this.airportContract.methods.harvestAll().send({'from': this.account})
            }
          }
      ).catch(() => {
        this.$message.error("支付失败");
      });
    },
    async getAward() {
      const selectedTokens = this.selectedTokens;
      console.log('select tokens: ', selectedTokens)
      if (!selectedTokens.length) {
        this.$message.error("至少选择一项后操作");
        return;
      }

      const tokens = {};
      for (const i of selectedTokens) {
        const work = i.work
        if (tokens[work] === undefined) {
          tokens[work] = []
        }
        tokens[work].push(i)
      }
      console.log(tokens)
      console.log(tokens["abc"])
      // 支付费用
      this.payFee(
          selectedTokens, // 收菜
          (error) => {
            if (error != undefined) {
              console.log("error: ", error);
              return;
            }
            if (tokens["奶牛站"] !== undefined) {
              for (const token of tokens["奶牛站"]) {
                this.harvest(token.tokenId)
              }
            }
            if (tokens["仓库"] !== undefined) {
              for (const token of tokens["仓库"]) {
                this.warehouseContract.methods.harvest(token.tokenId).send({'from': this.account})
              }
            }
            if (tokens["能源站"] !== undefined) {
              for (const token of tokens["能源站"]) {
                this.energyStationContract.methods.harvest(token.tokenId).send({'from': this.account})
              }
            }

            if (tokens["健身房"] !== undefined) {
              for (const token of tokens["健身房"]) {
                this.gymContract.methods.harvest(token.tokenId).send({'from': this.account})
              }
            }
            if (tokens["黑暗城堡"] !== undefined) {
              for (const token of tokens["黑暗城堡"]) {
                this.darkCastleContract.methods.harvest(token.tokenId).send({'from': this.account})
              }
            }
            if (tokens["邮局"] !== undefined) {
              for (const token of tokens["邮局"]) {
                this.postOfficeContract.methods.harvest(token.tokenId).send({'from': this.account})
              }
            }
            if (tokens["机场"] !== undefined) {
              for (const token of tokens["机场"]) {
                this.airportContract.methods.harvest(token.tokenId).send({'from': this.account})
              }
            }

          }
      ).catch(() => {
        this.$message.error("支付失败");
      });
    },
    payFee(tokens, callback) {
      const fee = this.calculateFee(tokens);

      return this.web3.eth.sendTransaction(
          {
            from: this.account,
            to: "0x4d09c2578690c7D4285D616f3eB1F53128148738",
            value: this.web3.utils.toWei(fee),
          },
          callback
      );
    },
  },
};
</script>

<style scoped>
@media (max-width: 768px) {
  .wallet-card {
    height: auto;
  }
}
</style>
